.win-bg-container {
  background-image: linear-gradient(to bottom, #9796f0, #fbc7d4);
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.result-card-container {
  background-image: linear-gradient(to right, #9796f0, #ffffff33);
  height: 50vh;
  width: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  padding: 20px;
}
.score {
  color: #6a59ff;
  font-size: 22px;
}
.button-play {
  background-color: #ffce27;
  border-width: 0px;
  color: #3d3d3d;
  font-size: 18px;
  width: 100px;
  height: 50px;
  cursor: pointer;
}
.win-img {
  height: 240px;
  margin-left: 20px;
}
