/* Write your code here. */
.nav-bg-container {
  background-color: #ffffff33;
  height: 15vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
.game-logo {
  width: 60px;
}
.emoji-nav-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.head {
  color: #ffffff;
  font-size: 25px;
}
.score-txt {
  color: white;
  font-size: 20px;
  font-weight: bold;
}
.score-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 20%;
}

@media (max-width: 768px) {
  .navbar-bg-container {
    background-image: #ffffff33;
    height: 15vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .game-logo {
    width: 40px;
    padding-left: 20px;
  }
  .head {
    color: #ffffff;
    font-size: 20px;
  }
  .score-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 50%;
  }
  .score-txt {
    color: white;
    font-size: 18px;
    font-weight: bold;
  }
}
